.block-rents {
  .round-digit {
    width: 141px;
    height: 141px;
    line-height: 141px;
    background: #f2f2f2;
    color: #fff;
    border-radius: 50%;
    text-align: center; }

  .title {
    color: #666666; }


  .card:hover {
    --tw-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow); }

  .card:hover> .round-digit {
    background: #f0c42e; }

  .card:hover .title {
    color: #333333; } }





